@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.socials{
  display: flex;
  align-items: center;
  &__item{
    @include lastM(0 2em 0 0);
    display: flex;
    align-items: center;
    &:hover{
      color: rgba(37, 211, 102, 1);
      .socials__icon{
        background: rgba(37, 211, 102, 0.2);
      }
    }
    &--tg{
      .socials__icon{
        background: rgba(59, 144, 206, 0.1);
      }
      &:hover{
        color: rgba(59, 144, 206, 1);
        .socials__icon{
          background: rgba(59, 144, 206, 0.2);
        }
      }
    }
  }
  &__icon{
    background: rgba(37, 211, 102, 0.1);
    width: 3em;
    height: 3em;
    border-radius: 50%;
    @include center;
    transition: all .3s;
    img{
      width: 1.5em;
    }
  }
  &__title{
    font-size: 1em;
    margin-left: .5em;
  }
}



@media screen and (max-width: 992px){
  .socials{
    &__item {
      @include lastM(0 .5em 0 0);
    }
    &__title{
      display: none;
    }
  }
}


