.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  input{
    border-radius: .67em;
    border: 1px solid #E9E7E7;
    padding: 0 1em;
    height: 3.55em;
    width: 100%;
    font-size: 1.125em;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #A3A8AE;
    }
    &:focus {
      border-color:$primary;
    }
  }
  ._error{
    &~.fg__error-text{
      display: block;
    }
  }
  &__error-text{
    z-index: 3;
    margin-top: -.0625em;
    color: red;
    position: absolute;
    font-size: .875em;
    display: none;
  }
}



@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .fg{
    input{
      font-size: 1em;
      height: 3em;
    }
  }
}
