.header{
  padding: 1.25em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    width: 17.875em;
    display: block;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    color: #777676;
    font-size: 1em;
    line-height: 150%;
    padding-left: 3em;
    margin-left: 3em;
    border-left: 1px solid #dcdcde;
    min-height: 4.5em;
    display: flex;
    align-items: center;
    max-width: 18.75em;
  }
  .socials{
    margin-left: auto;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &__logo{
      width: 11em;
    }
    &__desc{
      display: none;
    }
  }
}


