.h2{
  font-size: 2.25em;
  line-height: 89%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  font-weight: 700;
}

.text24{
  font-size: 1.5em;
  line-height: 133%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.bold-text{
  font-weight: 700;
}

.text-center{
  text-align: center;
}

.text{
  ul{
    @include grid100gap(1.5em);
    li{
      padding-left: 1.75em;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        width: 1.33em;
        height: 1.33em;
        left: 0;
        top: 0;
        background-image: url(../img/check.svg);
        @include bgImgSize(100%);
      }
      div{
        padding: 1em 0 0 1.75em;
      }
      p{
        font-size: 1em;
      }
    }
  }
  p,
  li{
    font-size: 1.5em;
    line-height: 133%;
  }
  p{
    @include lastM(0 0 1em);
  }
}



@media screen and (max-width: 992px){
  .h2{
    font-size: 2em;
  }

   .text18-tablet{
    font-size: 1.125em;
  }

  .text{
    font-size: .75em;
    ul{
      li{
        div{
          padding-left: 1em;
        }
      }
    }
  }
}
