.main{
  &__inner{
    background: #163fa7;
    position: relative;
    padding: 8.75em 4em;
    border-radius: 1.25em;
    color: $white;
  }
  &__props{
    max-width: 30em;
  }
  &__title{
    font-size: 6em;
    letter-spacing: -0.01em;
    text-transform: uppercase;
    font-weight: 700;
  }
  &__props{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
    margin-top: 1.6875em;
    max-width: 27em;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__img{
    position: absolute;
    top: -1.6875em;
    right: 2.875em;
    width: 51.5em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.main-prop{
  &--full{
    grid-column: 1/-1;
  }
  &__title{
    font-size: 1.0625em;
    line-height: 188%;
    opacity: .5;
  }
  &__desc{
    font-size: 2.25em;
    line-height: 89%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__desc2{
    font-size: 1.5em;
    line-height: 133%;
  }
}

.m-card{
  padding: 4em;
  background-color: $white;
  border-radius: 1.25em;
  width: 64.375em;
  max-width: 100%;
  margin: 0 auto;
}

.info-block{
  @include grid100gap(1.25em);
}

.req{
  margin-top: 1em;
  &__inner{
    position: relative;
  }
}

.req-success{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  @include center;
  flex-direction: column;
  z-index: 2;
  border-radius: inherit;
  background-color: inherit;
  padding: inherit;
  &__icon{
    width: 7.5em;
    margin-bottom: 1.875em;
  }
}

.req-form{
  margin: 2em auto 0;
  width: 30.75em;
  max-width: 100%;
  &._success{
    &~.req-success{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__fields{
    display: grid;
    grid-template-columns: 1fr 1.27fr;
    gap: .75em;
  }
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
}

._full{
  grid-column: 1/-1;
}

.place{
  margin-top: 1em;
}

.map{
  height: 23em;
  width: 100%;
  border-radius: .75em;
  margin-top: 2em;
  overflow: hidden;
  .ymaps-2-1-79-controls__control{
    left: unset !important;
    right: 1em !important;
    top: 8em !important;
    //transform: translateY(-50%);
    width: 3em !important;
    height: 7em !important;
    box-shadow: 0px 1.7px 5px 0px rgba(0, 0, 0, 0.04), 0px 0.9px 2.7px 0px rgba(0, 0, 0, 0.03), 0px 0.5px 1.5px 0px rgba(0, 0, 0, 0.03), 0px 0.3px 0.8px 0px rgba(0, 0, 0, 0.02), 0px 0.1px 0.3px 0px rgba(0, 0, 0, 0.01), 0px 4px 12px 0px rgba(0, 0, 0, 0.05);
    background-color: #fff;
    border-radius: 6.25em;
  }
  .ymaps-2-1-79-zoom{
    width: 100% !important;
    height: 100% !important;
    padding: 0;
  }
  .ymaps-2-1-79-zoom__scale{
    display: none;
  }
  .ymaps-2-1-79-zoom{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .ymaps-2-1-79-zoom__plus{
    top: 50%;
    transform: translateY(-100%);
    background-image: url(../img/plus.svg);
    border-radius: 6.25em 6.25em 0 0;
  }
  .ymaps-2-1-79-zoom__minus{
    bottom: 50% ;
    transform: translateY(100%);
    background-image: url(../img/minus.svg);
    border-radius: 0 0 6.25em 6.25em;
  }
  .ymaps-2-1-79-zoom__button{
    width: 100%;
    height: 50%;
    background-color: transparent;
    box-shadow: unset;
    border: none;
    border-radius: unset;
    padding: 0;
    @include bgImgSize(1.5em);
  }
  .ymaps-2-1-79-zoom__icon{
    display: none;
  }
}

.info{
  margin-top: 1em;
  .text{
    margin-top: 2.25em;
  }
}

.partners{
  margin-top: 1em;
  &__items{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 12em / 5));
    gap: 3em;
  }
}

.partner-item{
  &__img{
    margin: 0 auto .375em;
    width: 7.5em;
    height: 7.5em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      border-radius: 50%;
    }
  }
  &__text{
    text-align: center;
    font-size: .75em;
    line-height: 133%;
    color: #777676;
  }
}


@media screen and (max-width: 992px){
  .main{
    font-size: .625em;
    &__inner{
      padding: 6em 3em;
    }
    &__img{
      width: 40em;
    }
  }

  .m-card{
    padding: 1.5em 1em;
    border-radius: .75em;
  }

  .info-block{
    gap: .75em;
  }

  .partners{
    &__items{
      margin-top: 1.25em;
      grid-template-columns: repeat(5,calc(20% - 8em / 5));
      gap: 2em;
    }
  }

  .partner-item{
    font-size: .875em;
  }
}


@media screen and (max-width: 576px){
  .main{
    font-size: 1em;
    &__inner{
      border-radius: .75em;
      padding: 2em 1em;
    }
    &__img{
      width: 100%;
      position: static;
      margin-top: 1em;
    }
    &__title{
      font-size: 2.75em;
    }
    &__nav{
      margin-top: 1em;
    }
    &__btn{
      width: 100%;
    }
  }

  .main-prop{
    font-size: .75em;
    &__desc2{
      font-size: 1.25em;
    }
  }

  .req-success{
    &__icon{
      width: 5em;
      margin-bottom: 1em;
    }
  }

  .req-form{
    margin-top: 1em;
    &__fields{
      grid-template-columns: 100%;
    }
    &__nav{
      margin-top: 1em;
      .m-btn{
        width: 100%;
      }
    }
  }

  .partners{
    &__items{
      grid-template-columns: repeat(2,calc(50% - 1em));
    }
  }
}
