.footer{
  padding: 2.25em 0 3.75em;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__c,
  &__link,
  &__by{
    font-size: 1em;
    line-height: 150%;
    color: #777676;
  }
  &__c{
    margin-right: 2.125em;
  }
  &__by{
    margin-left: auto;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2em 0;
    &__inner{
      flex-direction: column;
      text-align: center;
    }
    &__c{
      margin: 0 0 1em;
    }
    &__by{
      margin: 1em 0 0;
    }
  }
}


