@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    height: 4em;
    border-radius: .75em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,15%);
        color: $white;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3em;
        border-radius: .5em;
        span{
            font-size: 1em;
        }
    }
}


